<template>
<div>

  <div style="margin-bottom: 30px">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">采样</el-breadcrumb-item>
      <el-breadcrumb-item>表格展示</el-breadcrumb-item>
    </el-breadcrumb>
  </div>

  <div style="margin: 10px 0">
    <el-input v-model="deviceId" placeholder="请输入设备ID" style="width: 200px"
              suffix-icon="el-icon-search"></el-input>
    <el-input v-model="sensorId" class="ml-5" placeholder="请输入传感器ID" style="width: 200px"
              suffix-icon="el-icon-search"></el-input>
    <el-input v-model="createDate" class="ml-5" placeholder="请输入采样时间" style="width: 200px"
              suffix-icon="el-icon-time"></el-input>
    <el-button class="ml-5" type="primary" @click="load">搜索<i class="el-icon-search"></i></el-button>
    <el-button type="warning" @click="reset">重置<i class="el-icon-refresh-right"></i></el-button>
    <el-button class="ml-5"  type="primary" @click="exportall">导出 <i class="el-icon-top"></i></el-button>
    <el-popconfirm
        cancel-button-text='我再想想'
        class="ml-5"
        confirm-button-text='确定'
        icon="el-icon-info"
        icon-color="red"
        title="您确定删除吗？"
        @confirm="delBatch">
      <el-button slot="reference" type="danger">批量删除 <i class="el-icon-remove-outline"></i></el-button>
    </el-popconfirm>
  </div>

  <div style="margin: 10px 0" class="ml-5">
    <el-button-group >
    <el-button type="primary" :plain='currSelect1' size="mini" :disabled='btnDisabledStart' @click="startRec">开始 <i class="el-icon-check"></i></el-button>
    <el-button type="primary" :plain='currSelect2' size="mini"  @click="stopRec">暂停 <i class="el-icon-close"></i></el-button>
    </el-button-group>
    <el-button type="primary" style=" margin-left: 63px">预警值</el-button>
    <el-input v-model="warnData" placeholder="请设定" style="width: 70px;"
              ></el-input>
    <el-button type="primary" style="margin-left: 66px">采样周期</el-button>
    <el-select v-model="value" placeholder="请选择" style="width: 85px;">
      <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
      </el-option>
    </el-select>

    <el-button type="primary" style="margin-left: 66px">采样对象</el-button>
    <el-select v-model="typevalue" placeholder="请选择" style="width: 85px;">
      <el-option
          v-for="item in typeoptions"
          :key="item.typevalue"
          :label="item.typelabel"
          :value="item.typevalue">
      </el-option>
    </el-select>

  </div>

  <el-table :data="tableData" :header-cell-class-name="headerBg" border stripe
            @selection-change="handleSelectionChange" >
    <el-table-column type="selection" width="55"></el-table-column>
    <el-table-column align="center" label="设备ID" prop="deviceId" width="200">
    </el-table-column>
    <el-table-column align="center" label="传感器ID" prop="sensorId" width="200">
    </el-table-column>
    <el-table-column align="center" label="采样值" prop="senValue" width="150">
      <template scope="scope">
        <span v-if="scope.row.senValue>=warnData" style="color:red">{{ scope.row.senValue }}</span>
        <span v-else style="color: #37B328">{{ scope.row.senValue }}</span>
      </template>
    </el-table-column>
    <el-table-column align="center" label="采样时间" prop="createDate">
    </el-table-column>
    <el-table-column align="center" label="操作" width="200">
      <template slot-scope="scope">
        <el-button type="success" @click="handleEdit(scope.row)">修改<i class="el-icon-edit"></i></el-button>
        <el-popconfirm
            cancel-button-text='我再想想'
            class="ml-5"
            confirm-button-text='确定'
            icon="el-icon-info"
            icon-color="red"
            title="您确定删除吗？"
            @confirm="del(scope.row.id)"
        >
          <el-button slot="reference" type="danger">删除 <i class="el-icon-remove-outline"></i></el-button>
        </el-popconfirm>
      </template>
    </el-table-column>
  </el-table>

  <div style="padding: 10px 0">
    <el-pagination
        :page-size="pageSize"
        :page-sizes="[10,20,50,100]"
        :total="total"
        current-page="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
    </el-pagination>
  </div>

  <el-dialog :visible.sync="dialogFormVisible" title="数据信息" width="30%">
    <el-form label-width="80px" size="small">
      <el-form-item label="设备ID">
        <el-input v-model="form.deviceId" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="传感器ID">
        <el-input v-model="form.sensorId" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="采样值">
        <el-input v-model="form.senValue" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="采样时间">
        <el-input v-model="form.createDate" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible = false">取 消</el-button>
      <el-button type="primary" @click="save">确 定</el-button>
    </div>
  </el-dialog>
  <div style="width: 1200px;height:580px;"></div>
</div>
</template>

<script>
export default {
  name: "Table",

  data() {
    return {
      options: [{
        value: 10,
        label: '10秒'
      }, {
        value: 15,
        label: '15秒'
      },{
        value: 20,
        label: '20秒'
      }, {
        value: 30,
        label: '30秒',
      }, {
        value: 60,
        label: '60秒'
      }],
      typeoptions: [{
        typevalue: 1,
        typelabel: '温度'
      }, {
        typevalue: 2,
        typelabel: '湿度'
      }],
      value:"",
      typevalue:"",
      btnDisabledStart:false,
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 10,
      deviceId: "",
      sensorId: "",
      senValue: "",
      createDate: "",
      dialogFormVisible: false,
      multipleSelection: [],
      form: {},
      currSelect1:true,
      currSelect2:true,
      warnData:"",
      headerBg: 'headerBg',

    }
  },
  created() {
    this.load()
  },
  methods:{


    startRec() {
      this.btnDisabledStart=true;
      if(this.typevalue===1){
        this.request.get("/data/start/"+this.value)
      }
      else if(this.typevalue===2){
        this.request.get("/data/startHum/"+this.value)
      }

    },

    stopRec() {
      this.btnDisabledStart=false;
      this.request.get("/data/stop")
    },

    exportall(){
      window.open("http://110.40.153.72:9090/data/export")
    },

    save() {
      this.request.post("/data/save", this.form).then(res => {
        if (res) {
          this.$message.success("修改成功")
          this.dialogFormVisible = false
          this.load()
        } else {
          this.$message.error("修改失败")
        }
      })
    },

    del(id) {
      this.request.delete("/data/delete/" + id).then(res => {
        if (res) {
          this.$message.success("删除成功")
          this.load()
        } else {
          this.$message.error("删除失败")
        }
      })
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    delBatch() {
      let ids = this.multipleSelection.map(v => v.id)
      this.request.post("/data/delete/batch", ids).then(res => {
        if (res) {
          this.$message.success("批量删除成功")
          this.load()
        } else {
          this.$message.error("批量删除失败")
        }
      })
    },

    load() {
      if(this.typevalue===1){
        this.request.get("/data/page", {
              params: {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                deviceId: this.deviceId,
                sensorId: this.sensorId,
                createDate: this.createDate,
              }
            }
        ).then(res => {
              this.tableData = res.records
              this.total = res.total
            }
        )
      }
      if(this.typevalue===2){
        this.request.get("/data/findHum", {
            }
        ).then(res => {
              this.tableData = res
            }
        )
      }
    },

    reset() {
      this.deviceId = ""
      this.sensorId = ""
      this.createDate = ""
      this.load()
    },

    handleEdit(row) {
      this.form = Object.assign({}, row)
      this.dialogFormVisible = true
    },

    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load()
    },

  },

}
</script>

<style >

</style>